import { Component, AfterViewInit } from '@angular/core';
import { ElementRef, ViewChild } from '@angular/core';
import { AnimationController } from '@ionic/angular';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements AfterViewInit {
  @ViewChild('mainCard', { read: ElementRef, static: true }) card: ElementRef;

  @ViewChild('personalProjectItem', { read: ElementRef, static: true })
  personalProject: ElementRef;
  @ViewChild('resumeItem', { read: ElementRef, static: true })
  resume: ElementRef;
  @ViewChild('programmingDocumentationItem', { read: ElementRef, static: true })
  programmingDocumentation: ElementRef;

  constructor(private animationCtrl: AnimationController) {}

  ngAfterViewInit() {
    const cardAnimation = this.animationCtrl
      .create()
      .addElement(this.card.nativeElement)
      .duration(2500)
      .fromTo('opacity', 0, 1);

    const personalProjectAnimation = this.animationCtrl
      .create()
      .addElement(this.personalProject.nativeElement)
      .duration(1700)
      .fromTo('transform', 'translateX(200px)', 'translateX(0px)');

    const resumeAnimation = this.animationCtrl
      .create()
      .addElement(this.resume.nativeElement)
      .duration(1700)
      .fromTo('transform', 'translateX(400px)', 'translateX(0px)');

    const programmingDocumentationAnimation = this.animationCtrl
      .create()
      .addElement(this.programmingDocumentation.nativeElement)
      .duration(1700)
      .fromTo('transform', 'translateX(600px)', 'translateX(0px)');

    cardAnimation.play();
    personalProjectAnimation.play();
    resumeAnimation.play();
    programmingDocumentationAnimation.play();
  }
}
